<template>
  <div>
    <Divider orientation="left">用户列表</Divider>
    <Table stripe border :columns="columns" :data="listData.data"></Table>
    <Spin size="large" fix v-if="load"></Spin>
    <div class="filter-btn">
      <Button style="font-weight: bold;margin: 10px 0" type="success" icon="md-add" @click="add()">新建</Button>
    </div>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'index',
  data () {
    return {
      formData: {},
      listData: {},
      load: false,
      operation_team_id: null,

      columns: [
        {
          title: '真实姓名',
          align: 'center',
          key: 'real_name'
        },
        {
          title: '手机',
          align: 'center',
          key: 'mobile'
        },
        {
          title: '角色',
          render: (h, params) => {
            return h('span', this.getRoles(params.row.role))
          }
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '禁用' : '正常')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.add(params.row.id)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }

      ]
    }
  },
  methods: {
    getRoles (list) {
      let str = ''
      if (list != null && list.length > 0) {
        list.forEach(value => {
          str += (value.name + ',')
        })
      }
      return str
    },
    loadData (page, id) {
      if (id !== null) {
        this.operation_team_id = id
      } else {
        id = this.operation_team_id
      }
      if (page) {
        this.formData.jump_page = page
      }
      this.formData.operation_team_id = id
      this.load = true
      api.adminList(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      },
      () => {
        this.load = false
      })
    },
    add (id) {
      this.$router.push({
        name: 'adminEdit',
        query: {
          id: id,
          operation_team_id: this.formData.operation_team_id

        }
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.name + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
