<style scoped>

</style>
<template>
  <div>
    <Divider orientation="left">管理模式列表</Divider>
    <Spin size="large" fix v-if="load"></Spin>
    <div class="filter-btn">
      <Button style="font-weight: bold;margin: 10px 0" type="success" icon="md-add" @click="add()">新建</Button>
    </div>
    <Table stripe border :columns="columns" :data="listData.data"></Table>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="parseInt(listData.per_page)"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from '../../api'

export default {
  data () {
    return {
      listData: {},
      load: false,
      operation_team_id: null,
      columns: [
        {
          title: '名称',
          align: 'center',
          key: 'flow_name'
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '未设置' : '已设置')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small',
                  disabled: params.row.is_edit !== 0
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.edit(params.row.id)
                  }
                }
              }, '编辑')
            ])
          }
        }
      ]
    }
  },
  mounted () {
  },
  methods: {
    loadData (page, id) {
      if (id !== null) {
        this.operation_team_id = id
      } else {
        id = this.operation_team_id
      }
      let data = { jump_page: page, operation_team_id: id }
      api.flowList(data, res => {
        this.listData = res.data.data.flow
      })
    },
    edit (id) {
      this.$router.push({ name: 'operationModeEdit', params: { flow_id: id } })
    }
  }
}
</script>
