<template>

  <div>
    <h3>管理员信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="80">

          <FormItem label="名称">
            <Input v-model="formData.name" placeholder="请填写团队名称"></Input>
          </FormItem>

          <FormItem label="简介">
            <Input v-model="formData.description" type="textarea" :rows="4" placeholder="请填写团队简介"></Input>
          </FormItem>

          <FormItem label="管理模式">
            <RadioGroup v-model="formData.management_type">
              <Radio :label="0">联合运营</Radio>
              <Radio :label="1">自营</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="负责人">
            <Select v-model="formData.leader_id">
              <Option v-for="(item,index) in leaders" :label="item.nickname" :value="item.id"
                      v-bind:key="index"></Option>
            </Select>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>
      <Col :span="24" v-if="$route.query.id != null">
        <Role ref="role"></Role>
        <Admin ref="admin"></Admin>
        <Mode ref="mode"></Mode>
      </Col>
      <Button type="success" style="width: 51%" @click="submit">提交</Button>

    </Row>
  </div>
</template>

<script>
import api from '../operating/api'
import Role from './components/role/index'
import Admin from './components/admin/index'
import Mode from './components/mode/index'
export default {
  components: {
    Role, Admin, Mode
  },
  name: 'edit',
  data () {
    return {
      formData: {},
      teams: [],
      roles: [],
      departments: [],
      leaders: [],
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
      this.loadLeader()
      this.$refs.role.loadData(1, this.$route.query.id)
      this.$refs.admin.loadData(1, this.$route.query.id)
      this.$refs.mode.loadData(1, this.$route.query.id)
    }
  },
  methods: {
    loadData () {
      this.load = true
      api.detail(
        { id: this.$route.query.id },
        res => {
          this.load = false
          this.formData = res.data.data
        }, () => {
          this.load = false
        })
    },
    loadLeader () {
      api.leader({ operation_team_id: this.$route.query.id }, res => {
        this.leaders = res.data.data
      })
    },
    submit () {
      if (!this.$formUtil.isStrValid(this.formData.name)) {
        this.$Message.error('请输入名称')
        return
      }

      if (!this.$formUtil.isStrValid(this.formData.description)) {
        this.$Message.error('请输入简介')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.management_type)) {
        this.$Message.error('请选择管理模式')
        return
      }

      this.load = true
      api.save(this.formData, res => {
        this.load = false
        this.$router.push({ name: 'operation_team' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

